* {
  margin: 0;
  padding: 0; }

body {
  font-family: "Montserrat Regular";
  font-size: 14px;
  color: #92a1a5;
  box-sizing: border-box; }

/*header*/
.header {
  display: flex; }
  .header__container {
    height: 100vh;
    width: 100%; }
  .header__info {
    position: absolute;
    z-index: 999;
    top: 20px;
    left: 40px; }
    .header__info ul {
      display: flex;
      align-items: center;
      list-style: none; }
      .header__info ul li {
        margin-right: 20px; }
        .header__info ul li #buy {
          margin-left: 800px;
          text-decoration: none;
          color: #fff;
          font-family: "Montserrat Bold"; }
  .header__left {
    position: relative;
    display: flex;
    flex-direction: column;
    top: 300px;
    left: 40px;
    width: 400px; }
    .header__left h3 {
      font-family: "Montserrat Bold";
      font-size: 50px;
      color: #000; }

.left,
.right {
  position: absolute;
  height: 100vh;
  width: 50%; }

.left {
  background: #eff2f6; }

.right {
  left: 50%;
  background: #ff343d; }

.circle {
  position: absolute;
  top: 50%;
  left: 50%;
  background: #fff;
  height: 400px;
  width: 400px;
  border-radius: 50%;
  transform: translate(-50%, -50%); }

.product {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: fly 4s ease-in-out infinite; }
  .product img {
    height: 305px;
    width: 530px; }

@keyframes fly {
  0% {
    transform: translate(-50%, -46%); }
  50% {
    transform: translate(-50%, -54%); }
  100% {
    transform: translate(-50%, -46%); } }

.about {
  display: flex;
  position: relative; }
  .about__left {
    transform: scaleX(0);
    transform-origin: left; }
    .about__left img {
      height: 415px;
      width: 100%;
      left: 0;
      top: 0; }
    .about__left_text {
      top: 160px;
      left: 130px;
      position: absolute; }
      .about__left_text h3 {
        font-family: "Montserrat Bold";
        font-size: 50px;
        color: #000; }
  .about__right_img {
    position: relative;
    height: 415px;
    width: 100%;
    left: 0;
    top: 0;
    transform: scaleX(0);
    transform-origin: right;
    background: url(/img/ways.png); }
    .about__right_img .img-overlay {
      width: 40%;
      height: 100%;
      background: rgba(0, 0, 0, 0.6); }
  .about__right_text {
    position: absolute;
    top: 160px;
    left: 50px; }
    .about__right_text h3 {
      font-family: "Montserrat Bold";
      font-size: 50px;
      color: #fff; }

/*features*/
.features {
  color: #000;
  font-family: "Montserrat Bold"; }
  .features__title {
    margin-top: 100px;
    margin-bottom: 90px;
    text-align: center;
    font-size: 38px; }
  .features__container {
    display: flex;
    justify-content: space-around; }
  .features__item {
    height: 95px;
    text-align: center;
    font-size: 20px; }
    .features__item p {
      margin-top: 15px;
      margin-bottom: 160px; }

/*New generation of vision*/
.newgen {
  display: flex;
  position: relative;
  margin-top: 200px; }
  .newgen__left img {
    height: 415px;
    width: 100%;
    left: 0;
    top: 0; }
  .newgen__right_img {
    position: relative;
    height: 415px;
    width: 100%;
    left: 0;
    top: 0;
    /*transform: scaleX(0);
    transform-origin: right;*/
    background: url(/img/artdron1.png); }
    .newgen__right_img h3 span {
      color: #ff343d; }
    .newgen__right_img .button {
      border-style: solid;
      border-width: 2px;
      border-color: #3b3b3b;
      background-color: transparent;
      border-radius: 35px;
      position: absolute;
      padding: 13px 15px;
      margin-top: 25px;
      text-align: center;
      text-decoration: none;
      color: #fff;
      font-size: 11px;
      font-family: "Montserrat Bold";
      transition: 0.3s; }
      .newgen__right_img .button:hover {
        background-color: #ff343d;
        border-color: #ff343d; }

/*New generation of flying*/
.newgenfly {
  display: flex;
  margin-top: 200px;
  justify-content: space-around; }
  .newgenfly__about h3 {
    font-family: "Montserrat Bold";
    font-size: 50px;
    color: #000; }
    .newgenfly__about h3 span {
      color: #ff343d; }
  .newgenfly__about .button {
    border-style: solid;
    border-width: 2px;
    border-color: #3b3b3b;
    background-color: transparent;
    border-radius: 35px;
    position: absolute;
    padding: 13px 15px;
    margin-top: 25px;
    text-align: center;
    text-decoration: none;
    color: #000;
    font-size: 11px;
    font-family: "Montserrat Bold";
    transition: 0.3s; }
    .newgenfly__about .button:hover {
      background-color: #ff343d;
      border-color: #ff343d;
      color: #fff; }
  .newgenfly__dron {
    width: 50%;
    position: relative;
    margin-left: 110px;
    transform: translateY(-50%);
    top: 100px;
    animation: flytwo 4s ease-in-out infinite; }
    .newgenfly__dron img {
      height: 400px;
      width: 100%; }

@keyframes flytwo {
  0% {
    transform: translateY(-46%); }
  50% {
    transform: translateY(-54%); }
  100% {
    transform: translateY(-46%); } }

/*Start your journey*/
.journey {
  background: url(/img/footerimg2.png);
  width: 100%;
  background-size: cover;
  height: 100vh;
  display: inline-block; }
  .journey__about {
    text-align: center; }
    .journey__about h3 {
      margin-top: 450px;
      font-size: 50px;
      font-family: "Montserrat Bold";
      color: #fff; }
    .journey__about .button {
      margin-top: 100px;
      display: inline-block;
      border: none;
      background-color: #fff;
      border-radius: 35px;
      color: #3e9f88;
      padding: 13px 15px;
      margin-top: 25px;
      text-align: center;
      text-decoration: none;
      font-size: 11px;
      font-family: "Montserrat Bold";
      transition: 0.1s; }
      .journey__about .button:hover {
        padding: 14px 16px; }

/*footer*/
.footer {
  text-align: center;
  margin: 120px 0; }
  .footer p {
    margin-top: 20px; }

#toTop {
  z-index: 999;
  width: 40px;
  height: 40px;
  border: none;
  font-size: 20px;
  border-radius: 3px;
  background: #fff;
  text-align: center;
  padding: 5px;
  position: fixed;
  bottom: 18px;
  right: 18px;
  display: none;
  color: #ff343d;
  cursor: pointer; }

@media only screen and (min-width: 768px) and (max-width: 1210px) {
  .header__container {
    position: relative;
    width: 100%;
    height: 400px;
    margin: 0;
    padding: 0; }
  .left,
  .right {
    position: absolute;
    height: 400px;
    width: 50%; }
  .circle {
    height: 200px;
    width: 200px; }
  .product img {
    height: 200px;
    width: 295px; }
  .header__info p {
    font-size: 10px; }
  .header__info ul li #buy {
    margin-left: 400px; }
  .header__left {
    top: 200px;
    left: 40px;
    width: 280px; }
    .header__left h3 {
      font-family: "Montserrat Bold";
      font-size: 40px;
      color: #000; }
    .header__left p {
      font-size: 12px; }
  .img-overlay {
    display: none; }
  .about__right_text .button {
    margin-top: 15px; }
  .newgenfly {
    margin-top: 100px;
    margin-left: 20px; }
    .newgenfly__dron {
      width: 50%;
      position: relative;
      margin-left: 110px;
      transform: translateY(-50%);
      top: 100px;
      animation: flytwo 4s ease-in-out infinite; }
      .newgenfly__dron img {
        height: 300px;
        width: 365px; }
  .journey {
    height: 400px; }
  .journey__about h3 {
    margin-top: 230px;
    font-size: 40px; }
  .footer {
    text-align: center;
    margin: 50px 0; }
    .footer p {
      margin-top: 15px; }
  .header {
    display: flex; }
    .header__container {
      height: 400px;
      width: 100%; } }

@font-face {
  font-family: 'Montserrat Regular';
  src: url("/fonts/montserrat/Montserrat-Regular.ttf"); }

@font-face {
  font-family: 'Montserrat Bold';
  src: url("/fonts/montserrat/Montserrat-Bold.ttf"); }
